import React, { useCallback } from 'react';
import { EMenuMode, Menu, SubMenu } from '@ali/we-design';

import { MenuBarProps } from './interface';
import { MenuBarWrapper } from './style';

/**
 * 菜单栏组件（横向排布）
 * @param props
 */
const MenuBar: React.FC<MenuBarProps> = (props) => {
  const { menus, style, ...rest } = props;
  const subMenus = menus.map((subMenu, index) => {
    const { title, children, ...subRest } = subMenu;
    return (
      <SubMenu {...subRest} title={title} key={`${subRest.testid}_${index}`}>
        {children}
      </SubMenu>
    );
  });

  /**
   * 阻止默认事件，防止文档区域失焦
   */
  const handleMouseDown = useCallback(ev => {
    ev.preventDefault && ev.preventDefault();
    ev.stopPropagation && ev.stopPropagation();
  }, []);

  return (
    <MenuBarWrapper onMouseDown={handleMouseDown} style={style}>
      <Menu {...rest} mode={EMenuMode.horizontal} selectedKeys={[]} destroyOverlayWhenInvisible>
        {subMenus}
      </Menu>
    </MenuBarWrapper>
  );
};

export default MenuBar;
